.currentPage {
  font-size: 50px;
  font-weight: bold;
}
.headerLinks {
  font-size: 20px;
  font-weight: normal;
  color: black;
}
.headerBar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 46px 80px 46px 80px;
}
.headerBar a {
  text-decoration: none;
  color: black;
  margin-right: 7px;
  border: 2px solid transparent;
  padding: 5px 10px 5px 10px;
}
.headerBar a:hover {
  border: 2px solid #886868;
}

@media screen and (max-width: 800px) {
  .headerBar {
    padding: 20px 40px 20px 40px;
    flex-direction: column;
    align-items: start;
  }
  .currentPage {
    font-size: 30px;
    padding-bottom: 10px;
  }
  .headerLinks {
    font-size: 15px;
  }
  .headerBar a {
    padding: 5px 3px 5px 3px;
  }
}
@media screen and (max-width: 1000px) {
  .headerBar {
    padding: 20px 40px 20px 40px;
  }
  .currentPage {
    padding-bottom: 10px;
  }
}
