.App {
  text-align: center;
  margin: 0;
  padding: 0;
}

.viewport {
  display: flex;
  justify-content: center;
  align-items: center;
  left: 50%;
  height: calc(100vh - 300px);
  width: calc(100vw - 80px);
  background-color: #f0f0f0;
  margin: 40px;
  margin-bottom: 0;
  overflow-y: scroll;
}

@media screen and (max-width: 800px) {
  .viewport {
    height: calc(100vh - 270px);
  }
}
