.homepage {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  padding: 20px;
}
.topText {
  font-size: 50px;
}
.bottomText {
  font-size: 60px;
}
.homepageText {
  text-align: end;
  flex: 3;
  animation: fade 5s infinite;
}
.pageImage {
  flex: 2;
  margin-left: 20px;
  display: flex;
  justify-content: left;
}
.pageImage > img {
  animation: zap 5s infinite;
}
.circle-small {
  width: 8px;
  height: 8px;
  background-color: #d9d9d9;
  border-radius: 50%;
  margin: 10px;
}
.slider-circles {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.slider-block {
  background-color: #886868;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  height: 21px;
  width: 8px;
  border-radius: 20px;
  position: absolute;
  top: calc(50% - 21px);
  animation: slide 20s linear infinite;
}
.slider {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-left: 20px;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  90% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
@keyframes zap {
  0% {
    transform: scaleX(0);
  }
  10% {
    transform: scaleX(0);
  }
  20% {
    transform: scaleX(1);
  }
  80% {
    transform: scaleX(1);
  }
  90% {
    transform: scaleX(0);
  }
  100% {
    transform: scaleX(0);
  }
}
@keyframes slide {
  2% {
    transform: translateY(0px);
  }
  24% {
    transform: translateY(0px);
  }
  27% {
    transform: translateY(28px);
  }
  49% {
    transform: translateY(28px);
  }
  52% {
    transform: translateY(56px);
  }
  74% {
    transform: translateY(56px);
  }
  77% {
    transform: translateY(84px);
  }
  99% {
    transform: translateY(84px);
  }
}

@media screen and (max-width: 1000px) {
  .slider-block {
    top: calc(50% - 66px);
  }
}
@media screen and (max-width: 800px) {
  .homepage {
    flex-direction: column;
    height: calc(100% - 40px);
  }
  .homepageText {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .pageImage {
    display: none;
  }
  .slider {
    margin-left: 0px;
    rotate: 90deg;
  }
  .slider-block {
    rotate: 180deg;
    margin-top: 28px;
    top: 50%;
  }
  .topText {
    font-size: 30px;
  }
  .bottomText {
    font-size: 40px;
  }
}
