.aboutPage {
  text-align: left;
  height: calc(100% - 120px);
  width: 100%;
  margin: 40px;
  margin-top: 60px;
  margin-bottom: 60px;
}
.about-block {
  display: flex;
  justify-content: center;
}
.about-text {
  flex: 2;
}
.experiences {
  flex: 3;
}
.experience-list {
  height: calc(100vh - 450px);
  overflow-y: scroll;
}
.experiences h3 {
  margin: 0;
  margin-top: 10px;
}
.experience p {
  margin: 0;
}
.about-block > .about-text {
  margin-right: 30px;
}
.about-text > img {
  max-height: 270px;
  margin-right: 30px;
  margin-bottom: 30px;
  float: left;
}

.myImage {
  justify-content: center;
  align-items: center;
  height: 100%;
}

@media screen and (max-width: 1000px) {
  .about-block {
    flex-direction: column;
  }
  .about-text > img {
    float: left;
    height: 150px;
    margin-left: 15px;
    margin-right: 15px;
    margin-top: 15px;
    margin-bottom: 15px;
  }
  .aboutPage {
    height: calc(100% - 20px);
    margin: 20px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
}

.experience-content {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.experience-button-box {
  margin-top: 20px;
  display: flex;
}
.experience {
  margin: 20px;
  border: solid 2px;
  padding: 20px;
}
.experience:hover {
  background-color: #fefefe;
}
.experience-image {
  flex: 1;
  display: flex;
  align-items: end;
  justify-content: start;
  height: 100%;
}
.experience-image > img {
  width: 100px;
  height: 100px;
  object-fit: contain;
  background-color: white;
  border: solid 2px;
}
.experience-text {
  flex: 3;
  margin-left: 8px;
}
.experience-button {
  padding: 10px;
  margin-right: 20px;
  border: 1px solid;
}
.experience-button:hover {
  border: 2px solid;
  padding: 9px;
  background-color: #fff;
}
@media screen and (max-width: 1000px) {
  .experience {
    margin: 0px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .experience-image > img {
    min-width: 100px;
    min-height: 100px;
    width: 12vw;
    height: 12vw;
    max-width: 150px;
    max-height: 150px;
  }
  .experience-text {
    flex: 4;
  }
  .experience-list {
    height: auto;
    overflow-y: auto;
  }
}

@media screen and (max-width: 600px) {
  .experience-button-box {
    flex-direction: column;
  }
  .experience-button {
    margin-top: 0;
    margin-right: 30px;
    margin-bottom: 10px;
    max-width: 120px;
    width: auto;
  }
}

@keyframes open-up {
  from {
    transform: scale(93%);
    opacity: 0;
  }
  to {
    transform: scale(100%);
    opacity: 1;
  }
}

@keyframes flip-flop {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.02);
  }
  100% {
    transform: scale(1);
  }
}

@media screen and (max-width: 700px) {
  .experience-content {
    padding: 20px;
    flex-direction: column;
    align-items: start;
  }
  .experience-text {
    margin-left: 0px;
  }
}
