.projectPage {
  text-align: left;
  height: 100%;
  width: 100%;
  margin: 0 40px 0 40px;
}
.projects {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

@media screen and (max-width: 1550px) {
  .projects {
    grid-template-columns: 1fr 1fr;
  }
}
.project-content {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.project-button-box {
  margin-top: 20px;
  display: flex;
}
.project {
  margin: 20px;
  border: solid 2px #666; 
  padding: 20px;
  animation: none;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.project:hover {
  background-color: #fefefe;
  animation: zoom-in-out 0.2s ease-in-out reverse;
}
.project-image {
  flex: 1;
}
.project-image > img {
  width: 150px;
  height: 150px;
  object-fit: contain;
  background-color: white;
  border: solid 2px;
}
.project-text {
  flex: 3;
  margin-left: 8px;
}
.project-button {
  padding: 10px;
  margin-right: 20px;
  border: 1px solid;
}
.project-button:hover {
  border: 2px solid;
  padding: 9px;
  background-color: #fff;
}
@media screen and (max-width: 1000px) {

  .projects {
    grid-template-columns: 1fr;
  }
  .projectPage {
    margin: 0 20px 0 20px;
    width: 100%;
  }
  .project {
    margin: 0px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .project-content {
    flex-direction: column;
    align-items: start;
  }
  .project-image > img {
    min-width: 100px;
    min-height: 100px;
    width: 12vw;
    height: 12vw;
    max-width: 150px;
    max-height: 150px;
  }
  .project-text {
    flex: 4;
  }
}

@media screen and (max-width: 600px) {
  .project-button-box {
    flex-direction: column;
  }
  .project-button {
    margin-top: 0;
    /* margin-left: 30px; */
    margin-right: 30px;
    margin-bottom: 10px;
    width: 110px;
  }
}

@keyframes open-up {
  from {
    transform: scale(93%);
    opacity: 0;
  }
  to {
    transform: scale(100%);
    opacity: 1;
  }
}

@keyframes zoom-in-out {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.02);
  }
  100% {
    transform: scale(1);
  }
}

@media screen and (max-width: 700px) {
  .project-text {
    margin-left: 0px;
  }
}

.project-hide {
  display: none;
}
.project-readmore {
  animation: flip-flop 1s;
}
.project-readmore:hover {
  animation: flip-flop 1s;
}
.project-readless {
  animation: flop-flip 1s;
}
.project-readless:hover {
  animation: flop-flip 1s;
}

@keyframes flip-flop {
  0% {
    transform: rotateY(0deg);
    opacity: 0;
  }
  to {
    transform: rotateY(360deg);
    opacity: 1;
  }
}
@keyframes flop-flip {
  from {
    transform: rotateY(0deg);
    opacity: 0;
  }
  to {
    transform: rotateY(-360deg);
    opacity: 1;
  }
}

.project-button-box a {
  display: flex;
  color: #000;
  text-decoration: none;
}
