.Footer {
  height: 20px;
  padding-bottom: 40px;
  padding-top: 40px;
  bottom: 0;
  z-index: 10;
  background-color: #fff;
  font-size: 15px;
  display: flex;
  align-items: start;
  justify-content: center;
}

.footer-content {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 40px;
  padding: 5px 20px 5px 0;
}

.footer-content > div {
  margin: 3px;
}

.footer-button {
  border: 1px solid #000;
  padding: 5px 20px;
}

.footer-button:hover {
  border: 2px solid #000;
  padding: 4px 19px;
}

.footer-content > .footer-dot {
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

@media screen and (max-width: 800px) {
  .footer-content {
    margin-left: 40px;
    justify-content: start;
    font-size: 15px;
    word-wrap: wrap;
    margin-top: 5px;
  }

  .footer-content > div {
    margin: 2px;
  }

  .footer-content > .footer-dot {
    width: 3px;
    height: 3px;
    margin: 3px;
  }

  .Footer {
    flex-direction: column;
    align-items: baseline;
  }
}

.contact-me-box-content {
  background-color: #fff;
  z-index: 20;
  position: fixed;
  top: calc(50% - 170px);
  left: calc(50% - min(175px, 35%));
  width: min(350px, 70%);
  height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 2px solid black;
}

.contact-me-box-content-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.contact-me-box {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(2px);
  display: flex;
  align-items: end;
  justify-content: center;
}

.contact-me-box-line {
  display: flex;
  align-items: center;
  justify-content: start;
  width: calc(100% - 40px);
  word-wrap: break-word;
  word-break: break-all;
  padding: 1px;

  padding-left: 20px;
  padding-right: 20px;
}

.contact-me-box-line:hover .contact-me-box-icon {
  background-color: #000;
}

.contact-me-box-icon {
  margin-right: 20px;
  width: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  background-color: #555;
  border-radius: 50%;
  height: 25px;
}

.contact-me-box-icon-descreption {
  display: flex;
  align-items: baseline;
}

@media screen and (max-width: 500px) {
  .contact-me-box-icon-descreption {
    flex-direction: column;
    align-items: baseline;
  }
}

.popup-close-button {
  padding: 10px;
  margin: 20px;
  border: 1px solid;
}

.popup-close-button:hover {
  border: 2px solid;
  padding: 9px;
}

.contact-me-box-content a {
  text-decoration: none;
  color: black;
  width: 100%;
}

.Footer a {
  text-decoration: none;
  color: #000;
  right: 0px;
  margin: 0 0;
}

.Footer a > .footer-content {
  margin-left: 0;
}
